@media (max-width:767.75px) {

    .header {
        width: 100%;
        height: 55px;
        line-height: 55pX;
    }

    .logo {
      margin-top: -.9rem;
    }

    .logo img {
      width: calc(100% - 77%);
  }

    .logo h5 {
        margin-top: -.65rem;
    }

    .cart__badge {
        top: 15px;
    }

    .nav__wrapper {
        position: relative;;
    }
    
    .menu {
        position: absolute;
        top: 100%;
        right: 0;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: #df2020;
        -webkit-clip-path: circle(0px at top right);
                clip-path: circle(0px at top right);
        transition: -webkit-clip-path ease-in-out 700ms;
        transition: clip-path ease-in-out 700ms;
        transition: clip-path ease-in-out 700ms, -webkit-clip-path ease-in-out 700ms;
        border-bottom-left-radius: 4.5rem;
      }
      

      .menu--open {
        -webkit-clip-path: circle(250% at top right);
                clip-path: circle(250% at top right);
        display: block;
        z-index: 999999999;
      }
      
      .menu a {
        border-bottom: 1px solid #EBEBD3;
        width: 100%;
        color: #EBEBD3;
        display: block;
        padding: 1.75rem 5.5rem 1.75rem 2rem ;
        text-transform: uppercase;
        transition: ease-in-out 450ms linear;;
      }

 
      .menu a:last-child {
        border-bottom: none;
      }
      
      .menu a:hover,
      .menu a:focus {
        background: #fde4e4;
        color: #df2020;
        /* border-color: #e8b24f; */
      }
      .menu a:hover:last-of-type,
      .menu a:focus:last-of-type {
        border-bottom-left-radius: 4.5rem;
      }

      .menu a .menu__link-icon {
        font-size: 1.75rem;
        margin-right: 1.75rem !important;
        margin-top: 0.3rem;
      }
      .menu a .menu__link-icon.last-icon {
        margin-top: 0.5rem;
      }

      .menu a.active__menu .menu__link-icon {
        margin-top: -.85rem;
      }
      .menu a.active__menu .menu__link-icon.last-icon {
        margin-top: -0.5rem;
      }
      
      .menu-toggle {
        padding: 1.65rem .75rem;
        position: relative;
        width: 100%;
        height: 100%;
        cursor: pointer;
      }
      
      .hamberger__icon,
      .hamberger__icon::before,
      .hamberger__icon::after {
        position: absolute;
        content: '';
        display: block;
        background: #212245;
        height: 3px;
        width: 1.5rem;
        border-radius: 3px;
        top: 50%;
        right: 0;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        transition: -webkit-transform ease-in-out 500ms;
        transition: transform ease-in-out 500ms;
        transition: transform ease-in-out 500ms, -webkit-transform ease-in-out 500ms;
      }
      
      .hamberger__icon::before {
        -webkit-transform: translateY(-7.5px);
                transform: translateY(-7.5px);
      }
      
      .hamberger__icon::after {
        -webkit-transform: translateY(4px);
                transform: translateY(4px);
      }
      
      .open .hamberger__icon {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
      }
      
      .open .hamberger__icon::before {
        opacity: 0;
      }
      
      .open .hamberger__icon::after {
        -webkit-transform: translateY(-1.75px) rotate(-90deg);
                transform: translateY(-1.75px) rotate(-90deg);
      }

}

@media (min-width: 768px) {

