  }

  .menu a:before {
    content: "";
    display: block;
    position: absolute;
    width: 0;
    height: 2.5px;
    background-color: #df2020;
    top: 63%;
    transition: .5s;
  }

  .menu a:hover {
    color: #df2020;
  }

  .menu a:hover::before {
    width: 100%;
  }

}



@media (max-width:575.75px) {
