.col__form {
    margin: auto !important;
    text-align: center !important;
}

.form {
    margin: auto !important;
}


.cart__item {
    padding-bottom: 10px;
    border-bottom: 5px solid #fdE4E4;
}

.cart__item-info {
    padding-bottom: 7px;
    border-bottom: 1px solid #fdE4E4;
}

.cart__item-info img {
    width: 35px;
    height: 35px;
    object-fit: cover;
}

.cart__product-title {
    font-size: .8rem;
    font-weight: 600;
}

.cart__product-price {
    font-size: .8rem;
}
.cart__product-price span {
    font-size: .9rem;
    font-weight: 600;
    color: #df2020;
}

.increase__decrease-btn {
    background: #fdE4E4;
    padding: 2px 5px;
    border-radius: 5px;
    width: 7rem !important;
}

.increase__btn,
.decrease__btn {
    cursor: pointer;

}

.delete__btn {
    font-size: 1.1rem;
    font-weight: 600;
    cursor: pointer;
}


.cart__container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.629);
    z-index: 99999;
}

.cart {
    position: absolute;
    top: 0;
    right: 0;
    width: 400px;
    height: 100%;
    background: #fff;
    z-index: 999999;
}

.cart__close {
    width: 100%;
    height: 50px;
    padding: 10px 20px;
}

.cart__close span .icon {
    font-size: 1.5rem;
    background: #000;
    padding: 3px;
    border-radius: 50%;
    color: #fff;
    cursor: pointer;
}

.cart__item-list {
    height: calc(100vh - 140px);
    overflow-y: scroll;
}

.cart__bottom {
    position: absolute;
    bottom: 15px;
    left: 0;
    padding: 5px 30px;
    width: 100%;
    height: 70px;
    background: #df2020;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.cart__bottom h6 {
    margin-bottom: 0;
    font-size: 1rem;
    color: #fff;
}

.cart__bottom h6 span {
    font-size: 1.3rem;
    font-weight: 600;
}

.cart__bottom button {
    border: none;
    padding: 5px 20px;
    border-radius: 5px;
    background: #fff;
    color: #212245;
}


/* google fonts */

/* reset default style */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

/* base style */

body {
  background: #fcfcfc;
  font-family: "Istok Web", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "RocknRoll One", sans-serif !important;
}

a {
  text-decoration: none;
  color: unset;
}

h1,
h2 {
  font-size: 2.1rem;
  font-weight: 600;
  color: #212245;
}

section {
  padding: 60px 0px;
}


@media (max-width:768px) {
  section {
    padding: 35px 0;
  }

  h1, h2 {
    font-size: 1.5rem;
  }
}
